export const Actions = {
    Types: {
        UPDATE_CHAT_LIST: "UPDATE_CHAT_LIST",
    },
    DispathUpdateChatListAction(chatList, dispatch) {
        dispatch({ type: this.Types.UPDATE_CHAT_LIST, data: chatList });
    },
    FetchChatList(offset, limit, state, dispatch) {
        this.DispathUpdateChatListAction([...state.ChatList, ...Array(limit).map(i => { })], dispatch);
    },
    RefreshChatList(limit, state, dispatch) {
        this.DispathUpdateChatListAction([...Array(limit).map(i => { })], dispatch);

    }
}