import React, { useContext, useRef, useState } from 'react';

import {
  Block, BlockTitle, f7, List, ListItem, Page, Navbar,
} from 'framework7-react';

import AppstorePage from '../components/AppstorePage';
import { StateContext } from '../state/StateContext';

const Search = () => {
  const { state, dispatch } = useContext(StateContext)
  console.log(dispatch);
  console.log(state.ChatList);

  const allowInfinite = useRef(true);

  const [showPreloader, setShowPreloader] = useState(true);

  const loadMore = () => {
    console.log("load")
    if (!allowInfinite.current) return;
    allowInfinite.current = false;

    if (state.ChatList.length >= 50) {
      setShowPreloader(false);
      return;
    }
    console.log(dispatch);
    dispatch({ type: "FETCH_MORE_CHAT_LIST" });
    allowInfinite.current = true;

  };

  return (

    <AppstorePage title="Music Chat">
      <Page ptr ptrMousewheel={true} onPtrRefresh={(done) => { console.log(123); dispatch({ type: "FETCH_REFRESH_CHAT_LIST" }); done() }}
        infinite infiniteDistance={50} infinitePreloader={showPreloader} onInfinite={loadMore}>
        <Block>Music Chat</Block>

        <List mediaList>
          {
            state.ChatList.map(chat => (
              <ListItem
                onClick={() => {
                  dispatch({ type: "JOIN_CHAT" });
                  dispatch({ type: "SHOW_CHAT_MESSAGE_WINDOW" })
                }}
                link='/chat/123'
                title="chat name"
                subtitle="music name">
                <img
                  slot="media"
                  src="https://cdn.framework7.io/placeholder/people-160x160-1.jpg"
                  width="80"
                />
              </ListItem>
            ))
          }
        </List>
      </Page>

    </AppstorePage>
  );
};

export default Search;
